import React from 'react';
import '../Modal.component.scss';

export default function ModalCookies({ handleClose }) {
  return (
    <div className="modal padding">
      <div className="container">
        <h1>GDPR</h1>
        <h2>Prelucrarea datelor cu caracter personal</h2>
        <p>
          Aceast politic de confidenialitate descrie modul i momentul
          &icirc;n care colectm, folosim i &icirc;mprtim informaiile
          dumneavoastr personale (numite in continuare &bdquo;informaii&rdquo;
          sau &bdquo;informaiile&rdquo;) atunci c&acirc;nd achiziionai un
          serviciu de la noi, ne contactai sau utilizai serviciile noastre
          &icirc;n alt mod, prin intermediul Facebook sau a website-ului nostru
          www.kineticstudio.ro.
        </p>
        <p>
          Aceast politic de confidenialitate nu se aplic practicilor unor
          tere pri pe care nu le deinem sau le controlm, inclusiv Facebook
          sau orice servicii tere pe care le accesai prin intermediul
          Facebook. Putei face referire la Politica de confidenialitate
          Facebook pentru a afla mai multe despre practicile sale de
          confidenialitate.
        </p>
        <h3>Informaiile pe care le colectm</h3>
        <p>
          Pentru a beneficia de serviciile noastre, trebuie s ne furnizai
          anumite informaii, cum ar fi numele dvs., numarul dvs. de telefonul,
          CNP-ul, adresa de e-mail, adresa locului in care se va efectua
          serviciul i informaiile de plat.
        </p>
        <h3>
          De ce avem nevoie de informaiile dvs. i de modul &icirc;n care le
          folosim?
        </h3>
        <p>
          Ne bazm pe o serie de temeiuri juridice pentru colectarea, utilizarea
          i distribuirea informaiilor, inclusiv:
        </p>
        <p>
          &bull; pentru necesitatea de a furniza serviciile noastre, cum ar fi
          atunci c&acirc;nd folosim informaiile dvs. pentru a &icirc;ndeplini o
          comand, pentru a rezolva eventualele dispute sau pentru a oferi
          sprijin clienilor;
        </p>
        <p>
          &bull; c&acirc;nd ne-ai furnizat consimm&acirc;ntul dvs., pe care
          &icirc;l putei retrage &icirc;n orice moment, pentru &icirc;nscrierea
          &icirc;n baza noastra de date de contact;
        </p>
        <p>
          &bull; dac este necesar s respectm o obligaie legal sau o
          hotr&acirc;re judectoreasc sau &icirc;n legtur cu o reclamaie
          legal, cum ar fi pstrarea informaiilor despre achiziiile dvs.,
          dac acest lucru este impus de legislaia fiscal; i
        </p>
        <p>
          &bull; dac este necesar &icirc;n scopul intereselor noastre legitime,
          dac aceste interese legitime nu sunt suprimate de drepturile sau
          interesele dvs., cum ar fi furnizarea i &icirc;mbuntirea
          serviciilor noastre. Noi folosim informaiile dvs. pentru a furniza
          serviciile pe care le-ai solicitat i &icirc;n interesul nostru
          legitim pentru a ne &icirc;mbunti serviciile.
        </p>
        <p>
          &bull; pentru a v oferi informaii despre promoii, actualizare de
          preuri sau alte servicii noi ce pot aprea ulterior.
        </p>
        <h3>Dezvluirea informaiilor</h3>
        <p>
          Informaiile despre clienii notri sunt importante pentru afacerea
          noastr. &Icirc;mprtim informaiile dvs. din motive foarte limitate
          i &icirc;n circumstane limitate, dup cum urmeaz:
        </p>
        <p>
          &bull; Prestatori de servicii. Angajm sau colaborm cu anumite pri
          tere, care se vor ocupa de prestarea serviciilor noastre. Vom
          &icirc;mprti informaiile dvs. personale acestor tere pri, dar
          numai &icirc;n msura &icirc;n care este necesar pentru a efectua
          aceste servicii.
        </p>
        <p>
          &bull; Transferuri de capital, v&acirc;nzarea afacerii. Dac vindem
          afacerea noastr sau fuzionm cu alta, putem divulga informaiile dvs.
          ca parte a tranzaciei respective, numai &icirc;n msura permis de
          lege.
        </p>
        <p>
          &bull; Respectarea legilor. Putem colecta, utiliza, reine i
          &icirc;mprti informaiile dvs., dac suntem convini c este
          &icirc;n mod rezonabil necesar: (a) s rspundem proceselor legale sau
          cererilor guvernamentale; (b) s punem &icirc;n aplicare acordurile,
          termenii i politicile noastre; (c) s previn, s investigheze i s
          abordeze fraude i alte activiti ilegale, de securitate sau de
          probleme tehnice; sau (d) s protejm drepturile, proprietatea i
          sigurana clienilor notri sau a altora.
        </p>
        <h3>Reinerea datelor</h3>
        <p>
          Pstrm informaiile dvs. personale numai at&acirc;ta timp c&acirc;t
          este necesar pentru a v oferi serviciile noastre i dup cum este
          descris &icirc;n Politica de confidenialitate. Cu toate acestea, s-ar
          putea s fim obligai s pstrm aceste informaii pentru a ne
          respecta obligaiile legale i de reglementare, pentru a rezolva
          disputele i pentru a ne pune &icirc;n aplicare acordurile. &Icirc;n
          general, pstrm datele dvs. pentru urmtoarea perioad de timp: 10
          ani.
        </p>
        <h3>Transferuri de informaii personale &icirc;n afara UE</h3>
        <p>
          Putem s stocm i s procesm informaiile dvs. prin intermediul
          serviciilor de gzduire ter parte din SUA i din alte jurisdicii.
          Prin urmare, putem transfera informaiile dvs. personale &icirc;ntr-o
          jurisdicie cu diferite legi de protecie a datelor i de guvernare
          dec&acirc;t jurisdicia dvs. Dac vom considera necesar s transferm
          informaii despre dvs. &icirc;n afara UE, ne bazm pe &bdquo;Scutul de
          Confidentialitate UE &ndash; SUA&rdquo; ca baz legal pentru
          transfer, deoarece Google Cloud si Facebook Inc. sunt certificate
          conform acestui acord.
        </p>
        <h3>Drepturile dumneavoastr</h3>
        <p>
          Dac locuii &icirc;n anumite teritorii, inclusiv &icirc;n UE, avei
          mai multe drepturi &icirc;n legtur cu informaiile dvs. personale.
          Dei unele dintre aceste drepturi se aplic &icirc;n general, anumite
          drepturi se aplic numai &icirc;n anumite cazuri limitate. Descriem
          aceste drepturi mai jos:
        </p>
        <p>
          &bull; Acces la informaii. Avei dreptul de a accesa i de a primi o
          copie a informaiilor personale pe care le deinem despre dvs.,
          contact&acirc;nd-ne, utiliz&acirc;nd informaiile de contact de mai
          jos.
        </p>
        <p>
          &bull; Modificai, restricionai, tergei. De asemenea, avei
          dreptul s modificai, s restricionai utilizarea sau s solicitati
          tergerea informaiilor dvs. personale pe care le deinem. &Icirc;n
          lipsa circumstanelor excepionale (de exemplu, &icirc;n cazul
          &icirc;n care suntem obligai s stocm date din motive legale), vom
          terge, &icirc;n general, informaiile dvs. personale, la cerere.
        </p>
        <p>
          &bull; Obiectai. Putei obiecta pentru (i) prelucrarea informaiilor
          dvs. in baza intereselor noastre legitime i (ii) primirea de mesaje
          de marketing de la noi dup ce ne-ai dat acordul expres pentru a le
          primi. &Icirc;n astfel de cazuri, vom terge informaiile dvs.
          personale, cu excepia cazului &icirc;n care avem motive convingtoare
          i justificate s continum s folosim aceste informaii sau dac este
          necesar din motive legale.
        </p>
        <p>
          &bull; Pl&acirc;ngeri. Dac locuii &icirc;n UE i dorii s faceti o
          notificare / plangere cu privire la folosirea informaiilor dvs. (i
          fr a aduce atingere oricror altor drepturi pe care le avei), avei
          dreptul s o facei cu autoritatea local de protecie a datelor.
        </p>
        <h3>Cum s ne contactai</h3>
        <p>
          &Icirc;n scopul aplicrii legii UE privind protecia datelor, noi,
          David Radu Mihai fizioterapeut, suntem administratorul datelor dvs.
          personale. Dac avei &icirc;ntrebri sau nelmuriri, putei s ne
          contactai la office@kineticstudio.ro sau la adresa: Str. Lt.
          Alexandru Popescu, nr. 9B, sector 3, Bucuresti.
        </p>
        <p className="close-modal" onClick={handleClose}>
          am luat la cunostinta
        </p>
      </div>
    </div>
  );
}
